'use client';

import { useState } from "react";
import Link from "next/link";
import { ArrowLeft } from "lucide-react";

export default function BookClient({ book }) {
    const [showForm, setShowForm] = useState(false);
    const [name, setName] = useState(book.name);
    const [author, setAuthor] = useState(book.author);
    const [description, setDescription] = useState(book.description || '');

    async function handleSubmit(e) {
        e.preventDefault();

        try {
            await fetch(`/api/books/${book.id}`, {
                method: 'PATCH',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ name, author, description }),
            });
            setShowForm(false);
        } catch (error) {
            console.error("Failed to update book:", error);
        }
    }

    async function handleDelete() {
        if (confirm("Are you sure you want to delete this book?")) {
            try {
                await fetch(`/api/books/${book.id}`, {
                    method: 'DELETE',
                });
                window.location.href = "/";
            } catch (error) {
                console.error("Failed to delete book:", error);
            }
        }
    }

    return (
        <div className="min-h-screen bg-gray-100 py-8 px-4 sm:px-6 lg:px-8">
            <div className="max-w-3xl mx-auto">
                <header className="mb-8">
                    <Link href="/">
                        <button className="bg-indigo-600 hover:bg-indigo-700 gap-2 text-white font-medium py-2 px-4 rounded-md flex items-center transition-colors">
                            <ArrowLeft />
                            Back to Library
                        </button>
                    </Link>
                </header>

                <main className="bg-white shadow-lg rounded-lg overflow-hidden">
                    <div className="p-6 sm:p-8">
                        <h1 className="text-3xl font-bold text-gray-900 mb-6">{book.name}</h1>

                        {!showForm ? (
                            <div className="space-y-4 mb-8">
                                <div className="flex items-center">
                                    <span className="text-gray-500 w-32">Author:</span>
                                    <span className="text-gray-900 font-medium">{book.author}</span>
                                </div>
                                <div className="flex items-center">
                                    <span className="text-gray-500 w-32">Status:</span>
                                    <span className="text-gray-900 font-medium">{book.status}</span>
                                </div>
                                {book.description && (
                                    <div className="pt-4 border-t border-gray-200">
                                        <h2 className="text-lg font-semibold text-gray-800 mb-2">Description</h2>
                                        <p className="text-gray-700 leading-relaxed">
                                            {book.description || 'No description available.'}
                                        </p>
                                    </div>
                                )}

                                <div className="flex gap-3 pt-6">
                                    <button
                                        className="bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-md"
                                        onClick={() => setShowForm(true)}
                                    >
                                        Edit Book
                                    </button>
                                    <button
                                        className="bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded-md"
                                        onClick={handleDelete}
                                    >
                                        Delete Book
                                    </button>
                                </div>
                            </div>
                        ) : (
                            <form onSubmit={handleSubmit} className="space-y-4 mb-8">
                                <input
                                    className="w-full p-2 border rounded"
                                    value={name}
                                    onChange={e => setName(e.target.value)}
                                    placeholder="Book Name"
                                />
                                <input
                                    className="w-full p-2 border rounded"
                                    value={author}
                                    onChange={e => setAuthor(e.target.value)}
                                    placeholder="Author"
                                />
                                <textarea
                                    className="w-full p-2 border rounded"
                                    value={description}
                                    onChange={e => setDescription(e.target.value)}
                                    rows={4}
                                    placeholder="Description"
                                ></textarea>

                                <div className="flex gap-2">
                                    <button type="submit" className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded">Save</button>
                                    <button
                                        type="button"
                                        className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded"
                                        onClick={() => setShowForm(false)}
                                    >
                                        Cancel
                                    </button>
                                </div>
                            </form>
                        )}
                    </div>
                </main>
            </div>
        </div>
    );
}